@import url("https://fonts.googleapis.com/css?family=Italianno&display=swap");
@import url("https://fonts.googleapis.com/css?family=Inria+Serif&display=swap");
body, html, #root, .App {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%; }

.logo-wrapper {
  float: right; }
  .logo-wrapper h1 {
    padding-right: 80px;
    font-family: 'Italianno', cursive;
    font-size: 50px; }

.project-list {
  padding-top: 250px;
  padding-left: 100px; }
  .project-list h1 {
    padding-left: 25px;
    font-family: 'Inria Serif', serif;
    font-size: 50px;
    color: #e27d60; }
  .project-list ul {
    margin: 0;
    list-style-type: none;
    padding-left: 25px; }
  .project-list li {
    padding-bottom: 10px; }
    .project-list li:last-child {
      padding-bottom: 0; }
  .project-list a {
    text-decoration: none;
    color: #d9c4bf;
    font-family: 'Inria Serif', serif;
    font-size: 20px; }
    .project-list a:visited {
      color: #d9c4bf; }
    .project-list a:hover {
      text-decoration: underline; }

.home-page {
  background-image: url("../../assets/alexandre-godreau-_YEBZiTgZS8-unsplash.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  min-width: 100%;
  min-height: 100%; }
